import { BrowserRouter, Link, Outlet, Route, Routes } from "react-router-dom";

function About() {
  return (
    <main>
      <h1>About us</h1>
    </main>
  );
}
function Home() {
  return (
    <div>
      <h1>Home</h1>
      <nav>
        <Link to="/">Home</Link> | <Link to="about">About</Link>
      </nav>
      <Outlet />
    </div>
  );
}

export default function App() {
  return (
    <Routes>
      <Route path="/about" element={<Home />}></Route>
    </Routes>
  );
}
